own in the dashboard page. Specifically, under the relevant users box. This\n    effect streches horizontally. Pulse3 is designed for the \"Discover More\" buttons. It is similar to the\n    hover1 with the difference that it takes a longer time and the background color is different.*/\n/*  Pulse 1*/\n@-webkit-keyframes pulse {\n    0% {\n        -webkit-transform: scale(1, 1);\n                transform: scale(1, 1);\n        opacity: 0.5;\n        background: rgba(100, 100, 200, 0.3);\n    }\n    100% {\n        -webkit-transform: scale(1.05, 1.05);\n                transform: scale(1.05, 1.05);\n        opacity: 1;\n        background: rgba(100, 100, 200, 0.5);\n    }\n}\n@keyframes pulse {\n    0% {\n        -webkit-transform: scale(1, 1);\n                transform: scale(1, 1);\n        opacity: 0.5;\n        background: rgba(100, 100, 200, 0.3);\n    }\n    100% {\n        -webkit-transform: scale(1.05, 1.05);\n                transform: scale(1.05, 1.05);\n        opacity: 1;\n        background: rgba(100, 100, 200, 0.5);\n    }\n}\n/*.btn:hover {\n    animation: pulse 0.4s infinite alternate;\n}*/\nsvg text:hover {\n    -webkit-animation: pulse 0.4s infinite alternate;\n            animation: pulse 0.4s infinite alternate;\n}\n.small-box h2:hover {\n    -webkit-animation: pulse 0.4s infinite alternate;\n            animation: pulse 0.4s infinite alternate;\n}\n/*  Pulse 2*/\n@-webkit-keyframes pulse2 {\n    0% {\n        -webkit-transform: scale(1, 1);\n                transform: scale(1, 1);\n        opacity: 0.8;\n        /*background: rgba(84, 85, 212, 0.05);*/\n    }\n    100% {\n        -webkit-transform: scale(1, 1);\n                transform: scale(1, 1);\n        opacity: 1;\n        -webkit-box-shadow: 2px 2px 2px 2px rgba(212, 40, 60, 0.4);\n                box-shadow: 2px 2px 2px 2px rgba(212, 40, 60, 0.4);\n        /*background: rgba(84, 85, 212, 0.05);*/\n    }\n}\n@keyframes pulse2 {\n    0% {\n        -webkit-transform: scale(1, 1);\n                transform: scale(1, 1);\n        opacity: 0.8;\n        /*background: rgba(84, 85, 212, 0.05);*/\n    }\n    100% {\n        -webkit-transform: scale(1, 1);\n                transform: scale(1, 1);\n        opacity: 1;\n        -webkit-box-shadow: 2px 2px 2px 2px rgba(212, 40, 60, 0.4);\n                box-shadow: 2px 2px 2px 2px rgba(212, 40, 60, 0.4);\n        /*background: rgba(84, 85, 212, 0.05);*/\n    }\n}\n.relevant-users:hover {\n    -webkit-animation: pulse2 0.4s infinite alternate;\n            animation: pulse2 0.4s infinite alternate;\n}\n/*  Pulse 3*/\n@-webkit-keyframes pulse3 {\n    0% {\n        -webkit-transform: scale(1, 1);\n                transform: scale(1, 1);\n        opacity: 0.5;\n        background: rgba(0, 0, 0, 0.3);\n    }\n    100% {\n        -webkit-transform: scale(1.1, 1.1);\n                transform: scale(1.1, 1.1);\n        opacity: 1;\n        background: rgba(0, 0, 0, 0.5);\n    }\n}\n@keyframes pulse3 {\n    0% {\n        -webkit-transform: scale(1, 1);\n                transform: scale(1, 1);\n        opacity: 0.5;\n        background: rgba(0, 0, 0, 0.3);\n    }\n    100% {\n        -webkit-transform: scale(1.1, 1.1);\n                transform: scale(1.1, 1.1);\n        opacity: 1;\n        background: rgba(0, 0, 0, 0.5);\n    }\n}\n.btn-status:hover {\n    -webkit-animation: pulse3 1s infinite;\n            animation: pulse3 1s infinite;\n}\n.btn-tm:hover {\n    -webkit-animation: pulse3 1s infinite;\n            animation: pulse3 1s infinite;\n}\n/*  RESPONSIVE ELEMENTS*/\n/*  This section contains the setup I made for making the website responsive*/\n@media all and (max-width: 672px) {\n    /*    Modify the header     */\n    .header,\n    .header-menu {\n        height: 250px;\n        width: 100%;\n        clear: both;\n    }\n    .header-menu .btn,\n    .name {\n        width: 100%;\n        border-radius: 10%;\n    }\n    .header-menu .btn a,\n    .name a {\n        display: block;\n        text-align: center;\n    }\n    .btn.name {\n        margin-left: 0px;\n    }\n    /*    Modify the container  */\n    .right-panel,\n    .right-panel-dashboard,\n    .box,\n    .home,\n    .container,\n    .home-body.container,\n    .four {\n        float: left;\n        width: 100%;\n        max-width: 95%;\n        min-width: 95%;\n        margin: 2.5%;\n        padding: 0px;\n        clear: both;\n    }\n    .box-wordcloud {\n        max-width: 500px;\n        min-width: 200px;\n        float: left;\n        clear: both;\n        margin-right: 2%;\n    }\n    .right-panel-profile {\n        float: left;\n        width: 100%;\n        margin-left: 0%;\n    }\n    #list-items {\n        font-size: 12px;\n    }\n}\n@media all and (max-width: 880px) {\n    /*    Modify the container  */\n    .right-panel,\n    .right-panel-dashboard,\n    .box,\n    .home,\n    .container,\n    .home-body.container,\n    .four {\n        float: left;\n        width: 100%;\n        max-width: 95%;\n        min-width: 95%;\n        margin: 2.5%;\n        padding: 0px;\n        clear: both;\n    }\n    .box-wordcloud {\n        max-width: 500px;\n        min-width: 200px;\n        float: left;\n        clear: both;\n        margin-left: 25%;\n        padding: 0px;\n        margin-top: 2px;\n    }\n    .right-panel-profile {\n        float: left;\n        width: 100%;\n        margin-left: 0%;\n    }\n    #list-items {\n        font-size: 16px;\n    }\n}\n/*This is used for the relevant tweets pannel*/\n.bs-callout {\n    padding: 20px;\n    margin: 20px 0;\n    border: 1px solid #eee;\n    border-left-width: 5px;\n    border-radius: 3px;\n}\n.bs-callout h4 {\n    margin-top: 0;\n    margin-bottom: 5px;\n}\n.bs-callout p:last-child {\n    margin-bottom: 0;\n}\n.bs-callout code {\n    border-radius: 3px;\n}\n.bs-callout+.bs-callout {\n    margin-top: -5px;\n}\n.bs-callout-default {\n    border-left-color: #777;\n}\n.bs-callout-default h4 {\n    color: #777;\n}\n.bs-callout-primary {\n    border-left-col